.Loader_loader__LG-Pq {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.Loader_loader__LG-Pq:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: rgb(63, 56, 56) transparent #fff transparent;
  -webkit-animation: Loader_loader__LG-Pq 1.2s linear infinite;
          animation: Loader_loader__LG-Pq 1.2s linear infinite;
}
@-webkit-keyframes Loader_loader__LG-Pq {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes Loader_loader__LG-Pq {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

